.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    height: 80vh;
}

.popular h1{
    color: #171717; 
    font-size: 50px;
    font-weight: 600;
}

.popular hr{
    width: 150px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.popular-item{
    margin-top: 40px;
    display:flex;
    gap: 30px;
}