.hero{
    height: 120vh;
    background: linear-gradient(180deg, #ba7fbc, #e1e1e43b 80%);
    display: flex;
}

.hero-left{
    flex: 1; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
    padding-left: 180px;
    line-height: 1.1; 
}

.hero-left h2{
    color: #090909;
    font-size: 50px;
    font-weight: 500;
}

.hero-left p{
    color: #171717;
    font-size: 70px;
    font-weight: 700;
}

.hero-hand-icon{
    display: flex;
    align-items:center;
    gap: 20px; 
}

.hero-hand-icon img{
    width: 105px;
}

.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-size: 22px; 
    font-weight:500;
}

.hero-right{
    max-width: 50%;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
} 